html,
body {
  padding: 0;
  margin: 0;
  font-family: "Poppins", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif; }

.container {
  width: 1280px;
  min-width: 1280px;
  margin: 0 auto; }
  .container .wrapper {
    padding: 0 48px; }
    .container .wrapper .home {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100%;
      width: 100%; }
      .container .wrapper .home p {
        font-size: 0.75rem;
        letter-spacing: 0.3rem;
        text-transform: uppercase;
        font-weight: 500; }
      .container .wrapper .home h5 {
        margin-top: 260px;
        font-size: 24px;
        font-weight: 600;
        padding-right: 360px; }

header {
  height: 100px; }
  header .inner-header {
    position: relative;
    z-index: 10;
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center; }
    header .inner-header .logo a {
      font-weight: 700;
      font-size: 1rem;
      text-decoration: none;
      color: #191919; }
    header .inner-header .menu button {
      border: none;
      background: none;
      outline: none;
      cursor: pointer;
      font-size: 0.8rem;
      mix-blend-mode: difference; }
  header .hamburger-menu {
    display: none;
    z-index: 9;
    top: 0;
    left: 0;
    right: 0;
    position: fixed;
    height: 100%;
    width: 100%; }
    header .hamburger-menu .menu-secondary-background-color {
      background: #191919;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      position: fixed;
      height: 100%;
      width: 100%;
      z-index: -1; }
    header .hamburger-menu .menu-layer {
      position: relative;
      background: #3b0fb4;
      height: 100%;
      overflow: hidden; }
      header .hamburger-menu .menu-layer .menu-compte-background {
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        position: absolute;
        height: 100%;
        width: 100%;
        opacity: 0;
        background-size: cover;
        background-repeat: no-repeat;
        -webkit-animation-name: backgroundEffect;
                animation-name: backgroundEffect;
        -webkit-animation-duration: 30s;
                animation-duration: 30s;
        -webkit-animation-iteration-count: infinite;
                animation-iteration-count: infinite; }
      header .hamburger-menu .menu-layer .wrapper {
        position: relative; }
        header .hamburger-menu .menu-layer .wrapper .menu-links {
          display: flex;
          justify-content: space-between;
          align-items: center;
          position: relative;
          top: 200px; }
          header .hamburger-menu .menu-layer .wrapper .menu-links nav {
            display: block; }
            header .hamburger-menu .menu-layer .wrapper .menu-links nav ul {
              margin: 0;
              padding: 0; }
              header .hamburger-menu .menu-layer .wrapper .menu-links nav ul li {
                list-style: none;
                font-size: 6rem;
                font-weight: 700;
                cursor: pointer;
                height: 135px;
                overflow: hidden;
                position: relative;
                width: 700px; }
                header .hamburger-menu .menu-layer .wrapper .menu-links nav ul li a {
                  position: absolute;
                  color: #fff;
                  text-decoration: none; }
                  header .hamburger-menu .menu-layer .wrapper .menu-links nav ul li a:hover {
                    color: #191919; }
          header .hamburger-menu .menu-layer .wrapper .menu-links .info {
            color: #fff;
            width: 300px; }
            header .hamburger-menu .menu-layer .wrapper .menu-links .info h3 {
              font-size: 1.2rem;
              margin: 8px auto; }
            header .hamburger-menu .menu-layer .wrapper .menu-links .info p {
              margin: 0 auto;
              font-size: 0.8rem; }
        header .hamburger-menu .menu-layer .wrapper .locations {
          position: absolute;
          bottom: -80px;
          color: #fff;
          margin-top: 16px;
          font-size: 0.8rem;
          font-weight: 600; }
          header .hamburger-menu .menu-layer .wrapper .locations .comptes {
            color: white;
            text-decoration: none;
            cursor: pointer;
            font-weight: 400;
            margin: 0 32px;
            -webkit-transition: 0.3s ease-in-out;
            transition: 0.3s ease-in-out; }
            header .hamburger-menu .menu-layer .wrapper .locations .comptes:first-child {
              margin-left: 64px; }
            header .hamburger-menu .menu-layer .wrapper .locations .comptes:hover {
              color: #fff;
              background: #191919;
              padding: 8px 24px;
              border-radius: 4px; }

@-webkit-keyframes backgroundEffect {
  0% {
    background-position: 0% 0%; }
  25% {
    background-position: 40% 10%; }
  50% {
    background-position: 0 10%; }
  75% {
    background-position: 10% 40%; }
  100% {
    background-position: 0% 0%; } }

@keyframes backgroundEffect {
  0% {
    background-position: 0% 0%; }
  25% {
    background-position: 40% 10%; }
  50% {
    background-position: 0 10%; }
  75% {
    background-position: 10% 40%; }
  100% {
    background-position: 0% 0%; } }

.login {
  display: grid;
  place-items: center;
  margin-left: auto;
  margin-right: auto;
  padding-top: 100px;
  padding-bottom: 100px;
}
.login > img {
  object-fit: contain;
  height: 70px;
  margin-top: 20px;
  margin-bottom: 20px;
}
.login > form {
  display: flex;
  flex-direction: column;
}
.login > form > input {
  width: 350px;
  height: 50px;
  font-size: 20px;
  padding-left: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
}
.login > form > button {
  width: 365px;
  height: 50px;
  font-size: large;
  color: #fff;
  background-color: #0074b1;
  border-radius: 5px;
}
.login__register {
  color: #0177b7;
  cursor: pointer;
}
.login > p {
  margin-top: 20px;
}

.formulaireInscription {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 1;
  background-color: rgb(247, 247, 247);
  z-index: 10;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
}

.userInscription {
  border: 1px solid black;
  width: 40%;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
  margin: auto;
  align-items: center;
  display: flex;
  flex-direction: column;
}

.firstPart {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 95%;
  margin: auto;
}

.first p {
  margin-top: -10px;
  margin-left: 20px;
}
.closeBtn {
  cursor: pointer;
}
.separateur {
  width: 90%;
  margin: auto;
  height: 1px;
  background: rgb(185, 185, 185);
}

.secondPart {
  align-items: center;
  width: 95%;
  margin: auto;
}
.zoneMail,
.zoneNP,
.zonePassword {
  width: 90%;
  margin: auto;
  margin-top: 3%;
}
.zoneNP {
  display: flex;
  justify-content: space-between;
}
.zonePassword {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}
.zoneNP input,
.password1 input,
.password2 input {
